.headerText {
  align-items: center;
  border-bottom: 1px solid #707070;
  display: flex;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 23px;
  font-style: italic;
  font-weight: 800;
  justify-content: space-between;
  letter-spacing: 4px;
  padding: 3px 10px 3px 25px;
}

#selectFeature{
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 12px;
  width: 100%;
}

#shareMethod pre{
  color: blue;
  font-size: 15px;
  font-weight: 900;
  margin: 0;
  margin-bottom: 10px;
}

#shareMethod{
  display: flex;
  justify-content: space-between;
}
#shareMethod p{
  color: blue;
  font-size: 12px;
  margin: 0;
  margin-bottom: 10px;
}

#inputSharedFiles{
  display: flex;
  justify-content: center;
}

#inputSharedFiles label{
  width: 80%;
  text-align: center;
  cursor: pointer;
  margin: 0 50px ;
  background-color: aqua;
  padding: 2px 40px;
  border-radius: 5px;
}

#inputSharedFiles input{
  display: none;
}

#fileType{
  display: flex;
  padding: 0px 30px;
  margin-bottom: 6px;
  align-items: center;
  justify-content: space-between;
}

#fileType #logo:hover{
  background-color: rgba(0, 0, 0, 0.116);
}
#fileType #logo{
  font-size: 15px;
  border-radius: 50%;
  padding: 4px;
  cursor: pointer;
}
#fileType p{
  font-size: 15px;
  margin-bottom: 5px;
  color: black;
}

#downloadImgHover #logo{
  color: white;
  font-size: 50px;
}
#downloadImgHover{
  position: absolute;
  width: 100%;
  height: 97%;
  border-radius: 5px;
  top: 0 ;
  display: none;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.189);
}

#sharedImageRecieve:hover #downloadImgHover{
  display: flex;
}

#sharedImageRecieve{
  position: relative;
  cursor: pointer;
}

#sharedFileRecieve{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

#sharedDocRecieve a{
  font-size: 14px;
}

#sharedDocRecieve{
  border: 1px solid black;
  padding: 5px 10px;
  display: flex;
  align-items: center;
  gap: 10px;
}


#sharedDocRecieve #logo{
  font-size: 15px;
  border-radius: 50%;
  padding: 4px;
  cursor: pointer;
}

#sharedDocRecieve #logo:hover{
  background-color: rgba(0, 0, 0, 0.163);
}

#sharedImageRecieve img{
  width: 150px;
  border-radius: 5px;
  aspect-ratio: 1;
  
}

#inputSharedFiles label:hover{
  box-shadow: 0 0 5px grey;
}

#allSelectedFiles {
  display: flex;
  padding: 5px 10px;
  border: 1px solid black;
  box-shadow: 0 0 5px grey;
  justify-content: space-between;
  margin-top: 10px;
  align-items: center;
}

#allSelectedFiles #logo{
  color: red;
  cursor: pointer;
  border-radius: 50%;
  padding: 2px;
  
}
#allSelectedFiles #logo:hover{
  background-color: rgba(0, 0, 0, 0.108);
}

#allSelectedFiles p{
  color: black;
}

#selectFeature h6{
  margin: 0;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  width: 48%;
  text-align: center;
}

#selectFeature h6:hover{
  color: red;
  font-weight: 600;
  border-bottom: 2px solid red;
}

#noOfUser {
  display: flex;
  margin-top: 7px;
  align-items: center;
}

#noOfUser label {
  font-size: 15px;
  margin-right: 10px;
  font-weight: 600;
}

#talkieHeaderLogo {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  cursor: pointer;
}

.headerText h4 {
  font-size: 23px;
  margin: 0;
}

.headerText p {
  color: blue;
  cursor: pointer;
  font-size: x-small;
}

.headerText p:hover {
  text-decoration: underline;
}

#groupForm {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

#groupForm input {
  width: 60%;
  padding: 3px 7px;
}
#groupForm label {
  font-size: 13px;
  text-align: center;
  letter-spacing: 1px;
  border: 1px solid black;
  padding: 3px 10px;
  border-radius: 5px;
  cursor: pointer;
}

#groupForm label:hover {
  background-color: black;
  color: white;
}
#groupForm input:nth-child(2) {
  display: none;
  width: 70%;
}

#ShareAFile button{
  background-color: brown;
  border-radius: 5px;
  padding: 3px; 
  color: white;
  text-align: end;
  cursor: pointer;
  font-weight: 500;
}

#chatId {
  background-color: #fff;
  box-shadow: 0 0 5px grey;
  display: none;
  padding: 20px;
  position: absolute;
  right: 10px;
  top: 25px;
  width: 480px;
  z-index: 999;
}

#chatId h3 {
  margin: 2px;
  padding-left: 10px;
}

#copyLogo {
  cursor: pointer;
  margin-right: 10px;
  border-radius: 50%;
  padding: 3px;
}
#copyLogo:hover {
  cursor: pointer;
  background-color: #0000001e;
  margin-right: 10px;
}

#addChatID {
  /* margin-top: 10px; */
  text-align: end;
  flex: 1;
}

#chatID {
  border: none;
  font-size: 17px;
  width: 350px;
}

#chatId span {
  font-family: sans-serif;
  font-size: 9px;
}

#randomNum {
  align-items: center;
  border-bottom: 3px solid red;
  display: flex;
  justify-content: space-between;
  padding-right: 20px;
}

#LogoTalkieChat {
  height: 25px;
}

#generateChatID:hover #chatId {
  display: block;
}
